*{color: #333; margin: 0; line-height: 160%;}
a{text-decoration: none;color: inherit;}

hr{margin-block: 20px;}
hr.class_line{border: 10px solid #eee; margin-block: 40px;}
h1.class_title{
  text-align: center;
  background: rgb(255, 239, 169);
  padding-block: 5px; margin-block: 100px 30px;
}
h2{
  text-align: center;
  color: rgb(47, 109, 145);
}

.text01{
  color: #0f0;
  font-size: 24px;
  font-weight: bold;
}
.text02{
  color: purple;
  font-size: 18px;
  font-weight: bold;
}

/* 컬리 실습 예제 */
.kully > ul{
  width: 100vw; max-width: 1000px;
  margin: 0 auto;
  display: flex; gap: 10px;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  font-size: 12px;
}
.kully_logo{
  text-align: center;
  padding-block: 30px;
  border-bottom: 1px solid #ccc;
}
.kully_title{
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  padding-block: 50px 30px;
}
.kully .act{
  background: #5f0080 !important;
  color: #fff;
}

/* 상품 이미지 서식 */
.kully > div{
  width: 100vw; max-width: 1000px;
  margin: 0 auto;
  display: flex; gap: 10px;
  justify-content: space-between;
  margin-block: 30px;
}
.kully > div > div{
  flex-grow: 1;
}
.kully > div > div img{
  width: 100%;
}
.kully > div > div h3{
  font-size: 16px;
  font-weight: normal;
  padding-block: 10px;
}
.kully > div > div p:nth-of-type(1){
  position: relative;
}
.kully > div > div p:nth-of-type(2){
  font-weight: bold;
}
.kully > div > div p:nth-of-type(2) span{
  color: orangered;
}
.kully > div > div p:last-of-type{
  color: #b3b3b3;
  text-decoration: line-through;
  font-size: 14px;
}

/* 컬러 실습예제 2 */
.kully_2 h3{
  text-align: center;
  margin-block: 10px;
}
.kully_2 ul{
  display: flex; gap: 10px;
  list-style: none;
}
.kully_2 li{flex: 1 1 0;}
.kully_2 li p:first-of-type{
  font-size: 18px;
  margin-bottom: 10px;
}
.kully_2 .sale{
  color: orangered;
}
.kully_2 .price{
  font-weight: bold;
}

/* 컬리 로그인폼 실습예제 서식 */
.kullyform{
  width: 400px; margin: 0 auto;
}
.kullyform fieldset{
  border: none;
}
.kullyform legend{
  text-align: center;
  font-weight: bold;
  font-size: 24px;
  padding-block: 50px 15px;
}
.kullyform label{
  display: none;
}
.kullyform input{
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
  border: 1px solid #666666;
  border-radius: 5px;
}
.kullyform input[type=text]{
  text-indent: 10px;
}
.kullyform .find_box{
  text-align: center;
  margin-block: 10px 40px;
}
.kullyform .find_box a{
  padding: 20px;
}
.kullyform input[type=submit], .kullyform input[type=button]{
  font-size: 16px; font-weight: bold;
}
.kullyform input[type=submit]{
  background: #5f0080;
  color: #fff;
  border: none;
}
.kullyform input[type=button]{
  background: #fff;
  border-color: #5f0080;
}
.kullyform .result{
  text-align: right;
}